@import "_grid.scss";
@import url('https://fonts.googleapis.com/css?family=Raleway');
@import url('https://fonts.googleapis.com/css?family=Raleway:800');

@for $i from 1 through 6 {
  .section#{$i} {
    height: 80vh;
    background: white;
  }
}

body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  width: 100%;

}

span {
  font-size: 16px;
}

a {
  color: rgba(0, 127, 211, 1);
}
a:active {
  color: rgba(0, 127, 211, 1);
}
//sekcja1

.navIcon {
  width: 50px;
  height: 50px;
  display: inline-block;
  padding-top: 10px;
}

//animacja hamburgera


.hamburger1, .hamburger2, .hamburger3 {
  width: 35px;
  height: 5px;
  background-color: white;
  margin: 6px 0;
  transition: 0.4s;
}

.change .hamburger1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px) ;
  transform: rotate(-45deg) translate(-9px, 6px) ;
}

.change .hamburger2 {opacity: 0;}

.change .hamburger3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px) ;
  transform: rotate(45deg) translate(-8px, -8px) ;
}

.menuMobile {
  width: 250px;
  height: 100vh;
  background-color: rgba(211, 211, 211, 0.9);
  position: fixed;
  top: 60px;
  right: 0px;
  margin-right: -300px;
  transition: all 1s;

}

.menuMobile li {
  color: rgba(0, 127, 211, 1);
  font-size: 1.5em;
  margin-left: 5px;
  margin-bottom: 30px;
  margin-top: 5px;
  list-style: none;
}
.menuMobileShow {
  width: 250px;
  height: 100%;
  background-color: rgba(211, 211, 211, 0.9);
  position:fixed;
  top: 60px;
  right: 0px;
  margin-right: 0;
  transition: all 1s;
  z-index: 1;

}

.mainRow {
  height: 70vh;
}

.section1 {
  position: relative;
  transition: all 1s;
  overflow: hidden;

}

//efektparalax dla tła nagłowka (section1)
.parallax {
  /* The image used */
  background-image: url("../images/background.jpg");

  /* Full height */
  height: 70vh;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media only screen and (max-width: 768px) {
  .parallax {
    background-size: 180%;
    background-image: url("../images/background.jpg");
    background-attachment: scroll;
    background-position: top;

  }
  .header {
    width: 100%;
    position: fixed;
    z-index: 1;

  }
}

.header {
  background: rgba(209, 209, 209, 0.8);
  height: 60px;
}



.nav {
  //border: 1px solid red;
  width: 90%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.logo {
  font-weight: lighter;
  color: rgba(0, 127, 211, 1);
  font-size: 0.9em;
  margin-left: 10px;

}
.menu {
  opacity: 1;
  color: rgba(9, 158, 215, 1);
  font-size: 0.8em;
  display: none;
}

a {
  text-decoration: none;
}

a:visited {
  color: rgba(0, 127, 211, 1);
}

.menu ul{
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu li {
  display: inline;
  padding-top: 20px;
  padding-bottom: 30px;
  margin-left: 20px;
}

.menu li:hover {
  text-decoration: underline;

}

.fullMenu {
  display: none;
}

//karuzela

.slider {
  width: 300px;
  height: 150px;
  position:relative;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  background-color: rgba(251, 251, 251, 0.5);
  margin-top: 50px;
  border-radius: 15px 15px;
  margin-left: 30px;

}

.sliderHiden {
  display: none;
}

.slide {
  font-family: 'Raleway', sans-serif;
  color: rgba(9, 158, 215, 0.8);
  font-size: 35px;
  //font-weight: 700;
  list-style: none;
  opacity: 0;
  z-index: 1;
  position:absolute;
    top: 20px;
    left: 20px;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s;
  border-radius: 15px 15px;
  padding: 10px;
}


#erka {
  font-size: 5px;
  margin: 0;
}

.showing {
  opacity: 1;
  z-index: 2;
}
//sekcja main

.main {
  display: flex;
  justify-content: center;
  background: rgb(249, 249, 249);
  font-size: 14px;
  color: gray;
  height: 50vh;
}

h2 {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 0;
  margin-left: 10px;
  transition: all 0.4s;
  font-family: 'Raleway', sans-serif;
  font-weight: lighter;
}

span {
  padding: 20px;
  display: inline-block;
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
}

.blueLine{
  background: rgba(9, 158, 215, 0.8);
  width: 150px;
  height: 3px;
  margin-bottom: 15px;
  margin-left: 10px;
  -webkit-transition: 0.4s; /* Safari */
  transition: all 0.4s;
}

.sectionX {
  width: 95%;
  height: auto;
  justify-content: center;
  margin: 10px;
  box-shadow: 6px 6px 8px 0 gray, 0 8px 25px 0 gray;
  overflow: auto;
  background-color: white;
  margin-top: 20px;
  margin-bottom: 20px;
}

.sectionX:hover {
  box-shadow: 10px 10px 12px 0 gray, 0 12px 30px 0 gray;
}

.sectionX:hover .blueLine {
  margin-left:30px;
}
.sectionX:hover h2 {
  margin-left: 30px;
}

